.admin-container {
    max-width: 900px;
    margin: 40px auto;
    background: white;
    border-radius: 12px;
    padding: 30px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
    from { transform: translateX(100%); opacity: 0; }
    to { transform: translateX(0); opacity: 1; }
}

.admin-header {
    display: flex;
    align-items: center;
    gap: 15px;
    border-bottom: 2px solid #eee;
    padding-bottom: 15px;
    margin-bottom: 20px;
}

.admin-header h2 {
    color: #1f2937;
    margin: 0;
}

.back-btn {
    background: none;
    border: none;
    cursor: pointer;
    color: #1f2937;
    padding: 5px;
    border-radius: 50%;
    transition: background 0.2s;
}

.back-btn:hover {
    background: #f0f0f0;
}

/* Tabla de Usuarios */
.user-list table {
    width: 100%;
    border-collapse: collapse;
}

.user-list th, .user-list td {
    padding: 12px 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.user-list th {
    background-color: #f7f7f7;
    color: #333;
    font-weight: bold;
}

/* Fila en modo edici√≥n */
.user-list tr.editing {
    background-color: #fffbe6;
}

.role-checkboxes {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    font-size: 14px;
}

.role-checkboxes label {
    display: flex;
    align-items: center;
    gap: 5px;
}

/* Botones de acci√≥n */
.user-list td:last-child button {
    margin-right: 10px;
    padding: 8px 15px;
    border-radius: 6px;
    font-size: 14px;
    cursor: pointer;
    border: none;
    transition: background 0.2s;
}

.user-list .btn-edit {
    background-color: #1f2937;
    color: white;
}
.user-list .btn-edit:hover {
    background-color: #000;
}

.user-list .btn-save {
    background-color: #0b3d0b;
    color: white;
}
.user-list .btn-save:hover {
    background-color: #062206;
}

.user-list .btn-cancel {
    background-color: #8b0000;
    color: white;
}
.user-list .btn-cancel:hover {
    background-color: #4d0000;
}

/* --- Formulario de Creaci√≥n de Roles --- */

.new-role-form-container {
    padding: 20px;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    margin-bottom: 25px;
    background-color: #f9fafb;
}

.new-role-form-container h3 {
    margin-top: 0;
    margin-bottom: 15px;
    color: #1f2937;
    font-size: 1.2em;
}

.new-role-form {
    display: grid;
    gap: 15px;
    grid-template-columns: 1fr; /* Por defecto una columna */
}

.new-role-form label {
    font-weight: 600;
    color: #4b5563;
    display: block;
    margin-bottom: 5px;
}

.new-role-form .input-group {
    display: flex;
    align-items: center;
}

.new-role-form .role-prefix {
    background-color: #e0e7ff;
    color: #4338ca;
    padding: 8px 10px;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
    border: 1px solid #c7d2fe;
    border-right: none;
    font-weight: bold;
    font-size: 14px;
}

.new-role-form input[type="text"],
.new-role-form textarea {
    flex-grow: 1;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    font-size: 14px;
    color: #1f2937;
    transition: border-color 0.2s, box-shadow 0.2s;
    min-height: 40px; /* Asegura altura m√≠nima para inputs */
}

.new-role-form input[type="text"] {
    border-radius: 0 6px 6px 0;
}

.new-role-form textarea {
    border-radius: 6px;
    resize: vertical;
}

.new-role-form input[type="text"]:focus,
.new-role-form textarea:focus {
    border-color: #4338ca;
    box-shadow: 0 0 0 3px rgba(67, 56, 202, 0.1);
    outline: none;
}

.new-role-form .btn-create-role {
    background-color: #4338ca;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    margin-left: 10px;
    cursor: pointer;
    font-weight: 600;
    transition: background-color 0.2s;
    white-space: nowrap;
}

.new-role-form .btn-create-role:hover:not(:disabled) {
    background-color: #3730a3;
}

.new-role-form .btn-create-role:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* Distribuci√≥n en pantallas m√°s grandes */
@media (min-width: 768px) {
    .new-role-form {
        grid-template-columns: 1fr 1fr auto; /* Name | Description | Button */
        align-items: end;
    }
    
    .new-role-form .input-group input[type="text"] {
        /* Ocupa el espacio restante en su grid-area */
        width: 100%; 
    }
}

/* --- Gesti√≥n de Roles Existentes (Nueva Secci√≥n) --- */
.role-management-container {
    margin-top: 30px;
    padding: 20px 0;
}

.role-management-container h3 {
    margin-bottom: 15px;
    color: #1f2937;
    font-size: 1.2em;
}

.role-management-table {
    width: 100%;
    border-collapse: collapse;
}

.role-management-table th, 
.role-management-table td {
    padding: 10px 15px;
    text-align: left;
    border-bottom: 1px solid #eee;
}

.role-management-table th {
    background-color: #f7f7f7;
    font-weight: bold;
}

.protected-role-tag {
    background-color: #e5e5e5;
    color: #555;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.85em;
    font-weight: 600;
}

.btn-delete-role {
    background-color: #dc2626; /* Rojo */
    color: white;
    padding: 6px 12px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
    transition: background-color 0.2s;
    display: inline-flex;
    align-items: center;
    gap: 5px;
}

.btn-delete-role:hover {
    background-color: #b91c1c;
}