/* ========================================================================== */
/* 1. LAYOUT GENERAL Y ESTRUCTURA (Para UserProfile.js) üß≠ */
/* ========================================================================== */

.profile-page {
  display: flex;
  margin: 0;
  font-family: 'Arial', sans-serif;
  min-height: 100vh;
  background-color: #f7f7f7;
}

/* -------------------------------
   Men√∫ lateral fijo (.sidebar)
--------------------------------*/
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 220px;
  height: 100vh;
  background-color: #1f2937; /* Azul gris√°ceo oscuro */
  color: white;
  z-index: 500;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.sidebar li {
  padding: 15px 20px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  font-weight: bold;
  font-size: 15px;
  border-bottom: 1px solid #00000033;
}

.sidebar li:hover {
  background-color: #000000;
}

.sidebar li.active {
  background-color: #32cd32; /* Verde Lima */
  color: #1f2937;
}

/* Estilo para el bot√≥n dentro de <li> (Cerrar sesi√≥n/Eliminar) */
.sidebar li button {
  all: unset;
  display: block;
  width: 100%;
  font-weight: inherit;
  font-size: inherit;
  cursor: pointer;
  color: inherit;
  background: none;
  text-align: left;
}
/* Asegurar que el hover del padre se mantenga */
.sidebar li button:hover {
    color: white;
}


/* -------------------------------
   Contenido principal (.content)
--------------------------------*/
.content {
  margin-left: 220px; /* Margen igual al ancho del sidebar */
  padding: 30px;
  width: calc(100% - 220px);
  box-sizing: border-box;
  overflow-y: auto;
}

/* -------------------------------
   Responsive para el Sidebar
--------------------------------*/
@media (max-width: 768px) {
  /* -------------------------------
     Sidebar en M√≥viles (Fila Superior)
  --------------------------------*/
  .sidebar {
    position: relative;
    width: 100%;
    height: auto;
    flex-direction: row;
    overflow-x: scroll;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }

  .sidebar ul {
    display: flex;
    width: max-content;
  }

  .sidebar li {
    flex-shrink: 0;
    border-bottom: none;
    border-right: 1px solid #00000033;
    padding: 10px 15px;
    font-size: 14px;
    white-space: nowrap;
  }
  
  .sidebar li:last-child { border-right: none; }

  /* -------------------------------
     Contenido principal en M√≥viles
  --------------------------------*/
  .content {
    margin-left: 0;
    width: 100%;
    padding: 15px;
    padding-top: 20px;
  }
}