/* ========================================================================== */
/* DATOS PERSONALES - PersonalDataTab */
/* ========================================================================== */

.user-profile {
  max-width: 450px;
  background: white;
  border-radius: 12px;
  padding: 30px 35px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
}

.user-profile h2 {
  color: #1f2937;
  margin-top: 30px;
  margin-bottom: 20px;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
}

.field {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.field label {
  font-size: 14px;
  color: #555;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: bold;
}

.field input {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  transition: border-color 0.2s;
}

.field input:focus {
  border-color: #32cd32;
  outline: none;
}

.field span {
  padding: 10px 12px;
  background: #f0f0f0;
  border-radius: 8px;
  border: 1px solid #ddd;
  color: #333;
  display: block;
}

/* Botones de Perfil */
.profile-buttons {
  display: flex;
  gap: 15px;
  margin-top: 30px;
  justify-content: center;
  flex-wrap: wrap;
}

.profile-buttons button {
  padding: 12px 25px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.profile-buttons .btn-edit {
  background: #1f2937;
}
.profile-buttons .btn-edit:hover {
  background: #000;
  transform: translateY(-2px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
}

.profile-buttons .btn-save {
  background: #0b3d0b;
}
.profile-buttons .btn-save:hover {
  background: #062206;
  transform: translateY(-2px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
}

.profile-buttons .btn-cancel {
  background: #8b0000;
}
.profile-buttons .btn-cancel:hover {
  background: #4d0000;
  transform: translateY(-2px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.3);
}

/* Responsive */
@media (max-width: 768px) {
  .user-profile {
    padding: 20px;
    margin: 0;
  }

  .profile-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .profile-buttons button {
    width: 100%;
    font-size: 15px;
  }
}