.professional-calendar {
  max-width: 900px;
  margin: 30px auto;
  background: white;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.15);
}

.booking-info {
  margin-top: 20px;
  text-align: center;
}

.book-btn {
  padding: 10px 20px;
  background-color: #32cd32;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
}

.book-btn:hover {
  background-color: #0b3d0b;
}
